<template>
  <div class="messagecenter">
    <div>
      <mt-header>
        <a slot="left" @click="back">
          <mt-button icon="back">通知</mt-button>
        </a>
      </mt-header>
    </div>
    <div class="page-cell">
      <mt-cell title="消息" @click.native="test"></mt-cell>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { MessageBox } from 'mint-ui'
export default {
  name: 'messageCenter',
  computed: {
    ...mapGetters({
      center:'getMessageCenter'
    })
  },
  methods: {
    ...mapActions({
      getMessageCenter: 'getMessageCenter',
      changeTabSelected: 'changeTabSelected'
    }),
    back: function() {
      this.$router.push({path: '/index/mymenu'});
      //this.changeTabSelected('我的');
    },
    test: function() {
      MessageBox({
        message: 'message',
        showCancelButton: false
      });
    }
  },
  created(){
    this.getMessageCenter();
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



